<template>
    <div class="login">
        <form action="">
            <p>
                <input placeholder="请输入用户名" type="text" name="userName" ref="userInput">
            </p>
            <p>
                <input placeholder="请输入密码" type="password" name="password">
            </p>
            <p>
                <input type="submit" value="一键登录" @click="sendLogin"><!--提交触发该函数-->
            </p>
        </form>
        <router-link to="/" tag="div">返回首页>>></router-link>
    </div>
</template>
<script>
export default {
    data(){
        return {

        }
    },
    methods:{
        sendLogin(){//用的localStorage模拟ajax请求
            // 登录就是向后端发送请求，用local模拟
            // alert(this.$local);
            var userName = this.$refs.userInput.value;
            this.$local.save('miaov',{
                login:true, //代表已经登录
                userName:userName  //同时记录用户名
            })

            let redirect = this.$route.query.redirect
            if(!redirect){// 为假也就说明点击的是主页的登录，登录成功之后应该进入我的项目才对，体验更好
                redirect = 'project' 
            }
            this.$router.push('/'+redirect)// 登录之后跳转到首页
        }
    }
}
</script>

<style scoped>
    .login{
        /* border:1px solid red; */
        text-align:center;
        margin-top:50px;
    }
    p{
        margin:0;
    }
    input{
        width:300px;
        height:30px;
        box-shadow:1px 1px 1px gray;
    }
    .login p:nth-of-type(1) input{
        border-bottom:none;
        background-color:lightgoldenrodyellow;
    } 
    .login p:nth-of-type(2) input{
        border-bottom-color:transparent;
        background-color:lightgoldenrodyellow;
    }
    .login p:nth-of-type(3) input {
        background-color:#43ADFD;
    }
    .login div{
        font-size:10px;
        margin-top:20px;
    }
</style>



